<template>
  <div>
    <div class="pagebar-title">二次开发接口文档管理</div>

    <div class="module">
    <el-radio-group v-model="form.type" @change="getList()">
      <el-radio-button label="1">农产品分类管理接口</el-radio-button>
      <el-radio-button label="2">主体管理</el-radio-button>
      <el-radio-button label="3">主体下农产品管理</el-radio-button>
      <el-radio-button label="4">采集数据调取接口</el-radio-button>
      <el-radio-button label="5">设备从机和指令参数接口</el-radio-button>
      <el-radio-button label="6">网关设备接口</el-radio-button>
      <el-radio-button label="7">设备回调参数模板管理接口</el-radio-button>
    </el-radio-group>
    </div>

    <div class="module">
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="id" label="数据库ID" width="100"> </el-table-column>
        <el-table-column prop="name" label="接口名" width="180"> </el-table-column>
        <el-table-column prop="url" label="接口地址"> </el-table-column>
        <el-table-column prop="create_time" label="创建时间" width="250"> </el-table-column>
        <el-table-column prop="update_time" label="最近一次更新时间" width="250"> </el-table-column>
        <el-table-column label="更多" fixed="right" width="125" align="center">
          <template slot-scope="scope">
            <el-dropdown>
              <el-button type="primary">
                管理<i class="el-icon-arrow-down el-icon--right"></i>
              </el-button>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item @click.native="oepnPageInADDorEdit(scope.row)">修改</el-dropdown-item>
                <el-dropdown-item @click.native="del(scope.row.id)">删除</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </template>
        </el-table-column>
      </el-table>
    </div>

  </div>
</template>

<script>
import { List, Change } from "@/api/opendosc";
export default {
  name: "OpenApiDocs",
  data() {
    return {
      tableData: [],
      form:{
        type: 1
      }
    };
  },
  computed: {

  },
  methods: {
    oepnPageInADDorEdit(data) {
      if (data && data.id){
        this.$router.push({name: "apisAdd", query:{
          docid:data.id
        }});
      }
    },
    getList() {
      this.$http(
        List(this.form),
        res => {
          if (res.code == 200) {
            this.$message({
              message: res.msg,
              type: "success"
            });
            this.tableData = res.data;
          } else {
            this.$message({
              message: res.msg,
              type: "error",
              duration: 5 * 1000
            });
          }
        }
      );
    },
    del(id){
      this.$http(
        Change({
          id:id,
          set:1
        }),
        res => {
          if (res.code == 200) {
            this.$message({
              message: res.msg,
              type: "success"
            });
            this.getList()
          } else {
            this.$message({
              message: res.msg,
              type: "error",
              duration: 5 * 1000
            });
          }
        }
      );
    }
  },
  created() {
    this.getList()
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
</style>
