import request from '@/utils/request'

export function Getdata(data) {
    return request({
        url: '/opendosc/get',
        method: 'post',
        data: data
    })
}

export function Addandedit(data) {
    return request({
        url: '/opendosc/add_edit',
        method: 'post',
        data: data
    })
}

export function Change(data) {
    return request({
        url: '/opendosc/change',
        method: 'post',
        data: data
    })
}

export function List(data) {
    return request({
        url: '/opendosc/list',
        method: 'post',
        data: data
    })
}